import React, { useContext } from 'react';
import logo from './img/logo.png';
import logo_b from './img/logo_b.png';
import SearchIcon from '@material-ui/icons/Search';
import Button from '@material-ui/core/Button';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import './style.css';
import { ManageContext } from '../../App';
import fire from '../../Fire';
import {Link} from "react-router-dom";
  
const Header = () => {
    const [detinationArea, setDetinationArea,  whiteTheme, setWhiteTheme, loginUser, setLoginUser] = useContext(ManageContext); 
    const [anchorEl, setAnchorEl] = React.useState(null);

    const handleClick = (event) => {
      setAnchorEl(event.currentTarget);
    };
  
    const handleClose = () => {
      setAnchorEl(null);
    };
    
    const signOut=()=>{
      fire.auth().signOut().then(function() {
        setLoginUser(''); 
          console.log("singout sucssfully")
          // Sign-out successful.
        }).catch(function(error) {
          console.log(error)
        });
   }
  const userNameCss = whiteTheme ? "userName" : "userName username_b"; 
    return (
        <>
            <div className="headerWraper">
              <div className="headerArea middle">
                <div className="logo left"><img  src={whiteTheme ? logo_b : logo}/></div>  
                <div className="searchbox left">
               { !whiteTheme && <SearchIcon className="searchIcon left" /> }
                { !whiteTheme &&  <input className="srcInput left" type="text" placeholder="Search Your Destination"></input> }
                </div>
                <div className="menu-mainmenu-container right">
                    <ul>
                      <li><Link className={`${whiteTheme ? "black" : ""}`} to="/">Home</Link></li>
                      <li><Link className={`${whiteTheme ? "black" : ""}`} to="/">Destination</Link></li>
                      <li><Link className={`${whiteTheme ? "black" : ""}`} to="/">Blog</Link></li>
                      <li><Link className={`${whiteTheme ? "black" : ""}`} to="/">Contact</Link></li>
                      { !loginUser.name && <li> <Link  to="/login" className="login">Login</Link></li> }
                    </ul>
                    
               { loginUser.name && <Button className={userNameCss} aria-controls="simple-menu" aria-haspopup="true" onClick={handleClick}>{loginUser.name}</Button> }
                    {/* loginUser.name && <h5 className="userName">{loginUser.name}</h5> */}
                    <Menu
                      id="simple-menu"
                      anchorEl={anchorEl}
                      keepMounted
                      open={Boolean(anchorEl)}
                      onClose={handleClose}
                      className="shsPopMenu"
                    >
                      <MenuItem onClick={handleClose}>Profile</MenuItem>
                      <MenuItem onClick={handleClose}>My account</MenuItem>
                      <MenuItem onClick={()=>{handleClose() ; signOut()}}>Logout</MenuItem>
                    </Menu>
                </div>
              </div>  
        </div>  
        </>
    );
};

export default Header;