import React, { createContext, useState } from 'react';
import './App.css';
import Home from './Component/Home/Home';
import Header from './Component/Header/Header';
import Booking from './Component/Booking/Booking';
import Login from './Component/Login/Login'; 
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link
} from "react-router-dom";
import Hotelinfo from './Component/Hotelinfo/Hotelinfo';
import PrivateRoute from './Component/PrivateRoute/PrivateRoute';
import Error404 from './Component/Error404/Error404';

export const ManageContext = createContext(); 

function App() {
  const [detinationArea, setDetinationArea] = useState([]); 
  const [loginUser, setLoginUser] = useState([]); 
  const [whiteTheme, setWhiteTheme] = useState(false); 

  return (
    <ManageContext.Provider value={[detinationArea, setDetinationArea, whiteTheme, setWhiteTheme, loginUser, setLoginUser]}>
    
    <div className={`${whiteTheme ? "fwidth" : "app fwidth"}`}>
    
    <Router>
        <Header></Header>
       <Switch>
        <Route exact path="/">
            <Home />
          </Route>
          <Route path="/booking">
          <Booking></Booking>
          </Route>
          <PrivateRoute path="/hotelinfo">
          <Hotelinfo></Hotelinfo>
          </PrivateRoute>
          <Route path="/login">
            <Login/>
          </Route>
          <Route path="*">
            <Error404 />
          </Route>
        </Switch>
        </Router>
      </div>
      </ManageContext.Provider>
  )
}

export default App;
