import React, { useContext, useEffect } from 'react';
import { ManageContext } from '../../App';
import Displayhotel from '../Displayhotel/Displayhotel';
import GoGoogleMap from '../GoGoogleMap/GoGoogleMap';
import './style.css'; 

const Hotelinfo = () => {
    const [detinationArea, setDetinationArea,  whiteTheme, setWhiteTheme] = useContext(ManageContext); 
    const destinationMap =detinationArea.destination + ", Bangladesh"; 
    
    useEffect(() => {
        console.log(detinationArea);
       },[]);
    return (
        <div>
            <div className="hotelInfoWrap fwidth">
                <div className="middle">
                    <div className="infoWrapper left">
                        
                    <p>252 stays Apr 13-17 3 guest</p>
                        <h3>Stay in {detinationArea.destination}</h3>
                        <Displayhotel></Displayhotel>
                    </div>


                    <div className="mapWrapper right">
                        <GoGoogleMap destination = {encodeURIComponent(destinationMap.trim())}></GoGoogleMap>
                    </div>
            </div>
            <div className="space fwidth"></div>
        </div>
    </div>
    );
};

export default Hotelinfo;