import React from 'react';
 const GoGoogleMap=(props)=>{
    const map_url ='https://maps.google.com/maps?q='+props.destination+'&t=&z=13&ie=UTF8&iwloc=&output=embed';
    return (
        <div className="google-map-code">
          <iframe src={map_url}
           width="500" height="600" frameborder="0" style={{border:0}} allowfullscreen="" aria-hidden="false" tabindex="0"></iframe>
        </div>
    );
 }
 export default GoGoogleMap