import React, { useContext, useEffect, useState } from 'react';
import './style.css';
import { makeStyles } from '@material-ui/core/styles';
import TextField from '@material-ui/core/TextField';
import {
  BrowserRouter as Router,
  Link,
  useHistory
} from "react-router-dom";
import { ManageContext } from '../../App';


const useStyles = makeStyles((theme) => ({
    formControl: {
      margin: theme.spacing(1),
      minWidth: 120,
    },
    selectEmpty: {
      marginTop: theme.spacing(2),
    },
    container: {
        display: 'flex',
        flexWrap: 'wrap',
      },
      textField: {
        marginLeft: theme.spacing(1),
        marginRight: theme.spacing(1),
        width: 200,
      },
  }));


const Booking = () => {

    const classes = useStyles();
    const history = useHistory();
    const [destArea, setDestArea] =  useState({
      to: 'Dhaka',
      fromDate:'2020-09-24' ,
      toDate: '2020-09-24',
    });

    const toHandleChange = (event) => {
      setDestArea({
        ...destArea,
        to: event.target.value,
      })
      console.log(destArea.id);
     };

     const FromDateChange = (event) => {
      setDestArea({
        ...destArea,
        fromDate: event.target.value,
      })
     }
     const ToDateChange = (event) => {
      setDestArea({
        ...destArea,
        toDate: event.target.value,
      })
     }
    const [detinationArea, setDetinationArea,  whiteTheme, setWhiteTheme] = useContext(ManageContext); 

      useEffect(() => {
         setDestArea({id: detinationArea.id});
         setWhiteTheme(false);
         console.log(destArea);
        },[]);

        const handleClick =(e) => {
          e.preventDefault();
          //const manageData = {...detinationArea, }
          setDetinationArea({
            ...detinationArea, ...destArea
          })
          setWhiteTheme(true); 
          history.push("/hotelinfo");
        }

    return (
        <>
        <div className="bookingWrapper">
            <div className="middle">
                <div className="bookingInfo left">
                <h1>{detinationArea.destination}</h1>
                     <p>{detinationArea.about}</p>
                  </div>
                <div className="bookingAreaForm  right">
                    <form onSubmit={handleClick}>
                     <div className="origin fwidth">
                         <p>Origin</p>
                        <select id="originId" name="origin" onChange={toHandleChange}>
                            <option value="Dhaka">Dhaka</option>
                            <option value="Khulna">Khulna</option>
                            <option value="Barisal">Barisal</option>
                            <option value="Rajshai">Rajshai</option>
                            <option value="Comilla">Comilla</option>
                            <option value="Chandpur">Chandpur</option>
                            <option value="Chitagong">Chitagong</option>
                        </select>
                    </div>        
                    <div className="destination fwidth">
                    <p>Destination</p>
                        <select id="destinationId" name="destination" value={destArea.id}  disabled>
                            <option value="1"> Cox's Bazar</option>
                            <option value="2"  >Sreemongol</option>
                            <option value="3">Sundorban</option>
                        </select>
                    </div>         
<div className="datePicker fwidth">
      <TextField
        id="from"
        label="From"
        type="date"
        defaultValue="2020-09-24"
        className="formDate left"
        onChange={FromDateChange}
        InputLabelProps={{
          shrink: true,
        }}
      />
      
      <TextField
        id="to"
        label="To"
        type="date"
        defaultValue="2020-09-30"
        className="toDate right"
        onChange={ToDateChange}
        InputLabelProps={{
          shrink: true,
        }}
      />
      </div>
        <button className="submitBtn fwidth">Start Booking</button>
                    </form>
                </div>
                <div className="space fwidth"></div>
             </div>
        </div>
        </>
    );
};

export default Booking;