import React, { useContext } from 'react';
import {
    BrowserRouter as Router,
    Switch,
    Route,
    Link,
    Redirect,
    useHistory,
    useLocation
  } from "react-router-dom";
import { ManageContext } from '../../App';

const PrivateRoute=({ children, ...rest })=> {
    const [detinationArea, setDetinationArea,  whiteTheme, setWhiteTheme, loginUser, setLoginUser] = useContext(ManageContext); 

    return (
      <Route
        {...rest}
        render={({ location }) =>
         loginUser.email ? (
            children
          ) : (
            <Redirect
              to={{
                pathname: "/login",
                state: { from: location }
              }}
            />
          )
        }
      />
    );
  }
export default PrivateRoute;