import React, { useContext, useEffect, useState } from 'react';
import './style.css';
import Button from '@material-ui/core/Button';
import { makeStyles } from '@material-ui/core/styles';
import Icon from '@material-ui/core/Icon';
import ArrowForwardIcon from '@material-ui/icons/ArrowForward';
import Sajek from './img/Sajek.png'
import Sreemongol from './img/Sreemongol.png'
import Sundorbon from './img/sundorbon.png'
import NavigateNextIcon from '@material-ui/icons/NavigateNext';
import NavigateBeforeIcon from '@material-ui/icons/NavigateBefore';
import {ManageContext} from '../../App'
import {
    BrowserRouter as Router,
    Link
  } from "react-router-dom";
  
const useStyles = makeStyles((theme) => ({
    button: {
      margin: theme.spacing(1),
    },
  }));

  const hotels =[
      {
        id : 1,
        destination : "Cox's Bazar",
        about : "Cox’s Bazar is a town on the southeast coast of Bangladesh. It’s known for its very long, sandy beachfront, stretching from Sea Beach in the north to Kolatoli Beach in the south. Aggameda Khyang monastery is home to bronze statues and centuries-old Buddhist manuscripts. South of town, the tropical rainforest of Himchari National Park has waterfalls and many birds. North, sea turtles breed on nearby Sonadia Island.",
        img: require("./img/Sajek.png")
      },
      {
        id : 2,
        destination : "Sreemongol",
        about : "It is said the name Sreemangal (or Srimangal) is named after Sri Das and Mangal Das; two brothers who settled on the banks of the Hail Haor.[2] A copper plate of Raja Marundanath from the 11th century was found in Kalapur. During an excavation at Lamua, an ancient statue of Ananta Narayan was dug out. In 1454, the Nirmai Shiva Bari was built and still stands today. Srimangal thana was established in 1912. The central town later became a pourashava in 1935. In 1963, two peasants were killed by police officers which kicked off the Balishira peasant riots. ",
        img: require("./img/Sreemongol.png")
      },
      {
        id : 3,
        destination : "Sundarban",
        about : "The Sundarbans is a mangrove area in the delta formed by the confluence of the Ganges, Brahmaputra and Meghna Rivers in the Bay of Bengal. It spans from the Hooghly River in India's state of West Bengal to the Baleswar River in Bangladesh. Wikipedia",
        img: require("./img/sundorbon.png")
      }
  ]

  const imgs = [
    require("./img/Sajek.png"),
    require("./img/Sreemongol.png"),
    require("./img/sundorbon.png"),
  ];

const removeClasses=() => {
    var els = document.getElementsByClassName("selectArea");

    for (var i = 0; i < els.length; i++) {
      els[i].classList.remove("selectArea");
    }
  }

const selectCat = (val) => {
   var element = document.getElementById(val);
   removeClasses(); 
   element.classList.add("selectArea");
   console.log("selectCat")

}
var i=1; 

const nextButton=()=>{
    i++;
    var x = document.getElementById("destSeletBtn").childElementCount; 
     if(i>x){
      i=1; 
     }
     
     var id = "cat_"+i;
     removeClasses(); 
     document.getElementById(id).classList.add("selectArea");
     document.getElementById(id).click();
     console.log(hotels);
 
 }
 const prevButton = ()=> {
    i--;
    var x = document.getElementById("destSeletBtn").childElementCount; 
    if(i<1){
        i=x
    }
    
    var id = "cat_"+i;
    removeClasses(); 
    document.getElementById(id).classList.add("selectArea");
    document.getElementById(id).click()
    console.log(i);
 }

const Home = () => {
    const classes = useStyles();
    const [detinationArea, setDetinationArea, whiteTheme, setWhiteTheme] = useContext(ManageContext); 

    useEffect(() => {
        // Update the document title using the browser API
        setWhiteTheme(false);
        setDetinationArea(hotels[0]);
      },[]);
    return (
        <>
        <div className="homeWrapper">
            <div className="middle">
                <div className="bookingInfo left">
                     <h1>{detinationArea.destination}</h1>
                     <p>{detinationArea.about}</p>
                    <Link className="bookingBtn" to="/booking">
                        <span className="left">Booking</span> 
                        <ArrowForwardIcon className="btn_2 right"></ArrowForwardIcon>
                    </Link>
                </div>
                <div className="selectAreaButton  right">
                    <div id="destSeletBtn" className="buttonWrap fwidth">
                        <button onClick={()=>{selectCat("cat_1");setDetinationArea(hotels[0]); i=1;}} id="cat_1" class="areaDestination left selectArea"><img src={Sajek}></img><h3>COX'S BAZAR</h3></button>
                        <button onClick={()=>{selectCat("cat_2");setDetinationArea(hotels[1]); i=2;}} id="cat_2" class="areaDestination left"><img src={Sreemongol}></img><h3>SREEMONGOL</h3></button>
                        <button onClick={()=>{selectCat("cat_3");setDetinationArea(hotels[2]); i=3;}}  id="cat_3" class="areaDestination left"><img src={Sundorbon}></img><h3>SUNDORBON</h3></button>
                    </div>
                    <div className="slideControler">
                        <button onClick={prevButton} className="prvious"> <NavigateBeforeIcon className="btn_3"/> </button>
                        <button onClick={nextButton}  className="next"> <NavigateNextIcon  className="btn_3"/> </button>
                    </div>
                </div>
             </div>
        </div>
        </>
    );
};

export default Home;